export default {
	data() {
		return {
			askList: [],
			problem: '',
			goodsId: '',
		};
	},
	onLoad(options) {
		console.log("options", options);
		if (options == undefined) {
		} else {
			this.goodsId = options.id;
			this.loadData(this.goodsId);
		}
		
	},
	onShow(){
		this.loadData(this.goodsId);
	},
	
	onPullDownRefresh() {
		//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
		console.log('refresh');
		this.loadData(this.goodsId);
		setTimeout(function() {
			uni.stopPullDownRefresh(); //停止下拉刷新动画
		}, 1000);
	},
	
	methods: {

		loadData(goodsId) {
			var condition = {
				"mallId": this.$base_api.MALL_ID,
				"goodsId": goodsId
			}
			this.$minApi.uniapp_post(this.$base_api.selectAskAndReply, condition).then(res => {
				if (res.success) {
				this.askList = res.data;
				} else {
					this.$msg("获取问答列表信息失败{}", res.msg);
				}
			})
		},

		inputProblem(e) {
			this.problem = e.detail.value;
		},

		ask() {
			if(!this.global.isLogin(this)){
				this.$openPage('Auth');
				return;
			}
			if(this.problem === null || this.problem.trim() === "" || this.problem.length < 4){
				uni.showToast({
					icon:'none',
					title:"提问至少四个字符",
					duration:1000
				})
				return;
			}
			if(this.problem.length > 40){
				uni.showToast({
					icon:'none',
					title:"提问最多四十个字符",
					duration:1000
				})
				return;
			}
			var condition = {
				"mallId": this.$base_api.MALL_ID,
				"goodsId": this.goodsId,
				"problem": this.problem
			}
			this.$minApi.uniapp_post(this.$base_api.ask, condition).then(res => {
				if (res.success) {
					uni.showToast({
						title: '提问成功！',
						duration: 2000
					});
					this.problem = '';
					this.loadData(this.goodsId);
				} else {
					this.$msg("提交失败，可能原因，未授权{}", res.msg);
				}
			})
		},
		
		answer(index){
			let ask = this.askList[index];
			ask.goodsId = this.goodsId;
			ask.answerEntities = '';
			uni.navigateTo({
				url: '/pages/askandreply/answer?data= ' + encodeURIComponent(JSON.stringify(ask))
			});
		}
	}

}
